$small: 800px;
$header-bgcolor: #f4f2f8;

@mixin button-transparent() {
//  width: 87px;
//  height: 34px;
  padding: 8px 18px;
  border-width: 2px;
  background: linear-gradient(white, white) padding-box, 
  linear-gradient(0deg, #8BA4F9, #BC9CFF) border-box;
  border-radius: 22px;
  border-style: solid;
  border-color: transparent;
  cursor: pointer;
  font-weight: bold;
  font-size: 12px;
  text-transform: uppercase;
  color: #BC9CFF;
}



.site-header{
  position: relative;
  background-color: $header-bgcolor;
//  width: 70px;

  padding: 14px 20px;
}

.text-header{
  text-decoration: none;
  font-size: 14px;
  color: rgba(31, 32, 65, 0.5);
  font-weight: normal;
  padding-left: 20px;
}

.site-header__wrapper {
//  padding-top: 1rem;
//  padding-bottom: 1rem;


  @media (min-width: $small) {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 0;
    padding-bottom: 0;
  }
}

.wrapper {
  max-width: 1920px;
//  padding-left: 1rem;
//  padding-right: 1rem;
//  margin-left: auto;
//  margin-right: auto; 
}

.nav__toggle {
  display: none;
  list-style-type: none;


  @media (max-width: 799px) {
    display: block;
    position: absolute;
    right: 1rem;
    top: 18px;
    bottom: 18px;
    @include button-transparent();
  }
}

.nav__wrapper {
  @media (min-width: $small) {
    display: flex;
  }

  @media (max-width: 799px) {
    position: absolute;
 //   top: 100%;
    right: 0;
    left: 0;
    z-index: -1;
    background-color: $header-bgcolor;
    visibility: hidden;
    opacity: 0;
    transform: translateY(-100%);
    transition: transform 0.3s ease-out, opacity 0.3s ease-out;

    &.active {
      visibility: visible;
      z-index: 1;
      opacity: 1;
      //left: 1rem;
      transform: translateY(0);
//      padding: 0.5rem 1.5rem;
      
    }
  }
}

.button-comein{
  @include button-transparent();
}

.button-register{
  @include button-transparent();
  background: linear-gradient(0deg, #8BA4F9, #BC9CFF) border-box;
  color: #ffffff;
}

.material-icons__arrow{
  font-size: 24px;
  padding-left: 0px;
//  padding:0px;
//  margin:0px;
}
